.form-container {
    &.fixed {
        position: fixed;
        left: 50%;
        bottom: 25px;
        -webkit-transform: translate(-50%);
        transform: translate(-50%);
    }

    .sr-search-item {
        box-shadow: 1px 4px 10px #ccc;
        border-radius: 3px;
        margin: 2px;
        transition: all 0.25s;

        & + .sr-search-item {
            margin-left: 10px;
        }
        &:hover {
            transform: scale(0.95);
        }

        &.active {
            // background-color: var(--sr-secondary);
            border: 2px solid var(--sr-secondary);
            // box-shadow: 1px 2px 2px var(--sr-secondary);
            box-shadow: none;
        }
    }

    .form-input-wrapper {
        position: relative;
        .sr-form-clear {
            --size: 10px;
            font-size: 12px;
            position: absolute;
            top: 50%;
            right: 5px;
            transform: translateY(-100%);
        }
    }

    .sr-search-results {
        overflow: unset;
    }
}

.sr-new-word-list {
    --padding: 20px;

    color: rgb(175, 175, 175);

    @media (max-width: 750px) {
        & {
            --padding: 10px;
        }
    }

    &.sr-dark-mode {
        .sr-word-list .sr-word {
            // width: calc(100% / 5 - 25px);
            // padding: 10px;
            box-shadow: unset;

            background-color: var(--sr-white);
            box-shadow: 5px 6px 10px var(--sr-shadow-color);
            border: 1px solid var(--sr-white);
        }

        .form-container .form-input-wrapper {
            .sr-form-clear {
                background-color: transparent;
            }
            input {
                color: rgb(175, 175, 175);
            }
        }
    }
    .sr-new-word-list-container {
        height: 95%;
        justify-content: space-between;
        flex-direction: column;
        flex-flow: column;
        // align-content: space-between;
    }
    .sr-new-word-list-wrapper {
        flex: 1;
        display: flex;
        flex-wrap: nowrap;

        @media (max-width: 750px) {
            & {
                flex-direction: column;
            }
        }
    }
    .sr-sidebar {
        width: 30%;
        min-width: fit-content;
        max-height: 525px;
        background-color: var(--sr-white);
        box-shadow: 5px 6px 10px var(--sr-shadow-color);
        padding: var(--padding);

        @media (max-width: 750px) {
            & {
                max-width: 100%;
                width: 100%;
                min-width: unset;
            }
        }

        overflow: auto;
    }

    .sr-options {
        list-style: none;
        padding: 0;
        margin: 0;

        @media (max-width: 750px) {
            & {
                display: flex;
            }
        }

        .sr-options-name {
            cursor: pointer;
            margin-bottom: 10px;
            padding: 5px 0;
            // line-height: 20px;

            @media (max-width: 750px) {
                & + .sr-options-name {
                    margin-left: 20px;
                    padding: 8px;
                }
            }

            &:hover {
                text-decoration: underline;
            }

            &.active {
                color: var(--sr-primary);
                border-bottom: 1px solid;
                font-weight: bold;
            }
        }
    }

    .sr-main-content {
        flex: 1 1 100%;
        padding: var(--padding);
        overflow-y: auto;
        max-height: 500px;

        @media (max-width: 500px) {
            & {
                max-height: 300px;
            }
        }

        .sr-title {
            font-size: 16px;
            margin-top: 0;
            margin-bottom: var(--padding);
        }
    }

    .sr-word-list {
        list-style: none;
        display: flex;
        // align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        overflow: auto;
        // padding-left: 20px;

        .sr-word {
            position: relative;
            width: calc(100% / 5 - 25px);
            padding: calc(var(--padding) / 1.5);
            box-shadow: 1px 4px 10px #ccc;
            background-color: var(--sr-white);
            border-radius: 3px;
            margin: calc(var(--padding) / 2);
            transition: all 0.25s;
            border: 1px solid transparent;
            overflow: hidden;
            cursor: pointer;

            &:hover {
                .sr-delete-btn {
                    display: inline-block;
                    // opacity: 1;
                }
            }

            @media (max-width: 900px) {
                & {
                    width: calc(100% / 3 - 20px);
                }
            }
            @media (max-width: 500px) {
                & {
                    width: calc(100% / 1);
                }
            }

            & + .sr-search-item {
                margin-left: 10px;
            }
            &:hover {
                // transform: scale(0.95);
                border: 1px solid var(--sr-secondary);
            }

            &.active {
                // background-color: var(--sr-secondary);
                border: 2px solid var(--sr-secondary) !important;
                // box-shadow: 1px 2px 2px var(--sr-secondary);
                .sr-root-word {
                    // color: var(--sr-yellow);
                    color: var(--sr-primary) !important;
                }
                box-shadow: none;
            }
            &.pass-letter {
                // background-color: var(--sr-secondary);
                border: 2px solid var(--sr-yellow);
                // box-shadow: 1px 2px 2px var(--sr-secondary);
                box-shadow: none;
                transition: all 0.4s;

                .sr-root-word {
                    color: var(--sr-yellow);
                }
            }

            .sr-root-word {
                color: var(--sr-primary);
            }
            .sr-delete-btn {
                display: none;
                // opacity: 0;
                position: absolute;
                top: 0px;
                right: 0px;
            }
            .sr-word-families {
                margin-left: 15px;
                margin-top: 10px;
                list-style: none;
                padding-left: 10px;
                border-left: 2px solid var(--sr-secondary);
            }
        }
    }

    .form-container {
        margin: auto;
        margin-top: 20px;
        width: 100%;
    }
}

.sr-app {
    .nav-mobile {
        position: relative;
        // width: 100vw;
        // height: 100vh;
        top: unset;
        left: unset;
        transform: unset;
    }
}
